:root {
  font-size: 10px;
  --bg-color: #ddd8d8;
  --sub-color: #c2c1db;
  --text-color: #6f6c6c;
  --border-color: #fff;
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
